.button--func {
  background: transparent;
  border: 2px solid #19415a;
  color: #19415a;
  cursor: pointer;
  padding: 0.7rem 1.0rem;
  position: relative;
  transition: color 0.25s;
  z-index: 1; }
  .button--func:before, .button--func:after {
    background: #19415a;
    content: '';
    display: block;
    height: 50%;
    left: 0;
    position: absolute;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: -webkit-transform 0.25s;
    transition: transform 0.25s;
    transition: transform 0.25s, -webkit-transform 0.25s;
    width: 100%;
    z-index: -1; }
  .button--func:before {
    bottom: 0;
    -webkit-transform-origin: center bottom;
            transform-origin: center bottom; }
  .button--func:after {
    top: 0;
    -webkit-transform-origin: center top;
            transform-origin: center top; }
  .button--func:hover, .button--func:focus {
    color: white; }
    .button--func:hover:before, .button--func:hover:after, .button--func:focus:before, .button--func:focus:after {
      -webkit-transform: scaleY(1);
              transform: scaleY(1); }

.button--nav {
  color: inherit;
  display: inline-block;
  position: relative;
  text-decoration: none;
  transition-duration: 0.25s;
  transition-property: color, -webkit-transform;
  transition-property: color, transform;
  transition-property: color, transform, -webkit-transform;
  z-index: 1; }
  .button--nav:before, .button--nav:after {
    content: '';
    display: block;
    position: absolute; }
  .button--nav:before {
    bottom: -1rem;
    left: -1rem;
    right: -1rem;
    top: -1rem;
    z-index: -1; }
  .button--nav:after {
    background: currentColor;
    height: 0.15rem;
    opacity: 0;
    left: 0;
    top: calc(100% + 0.25rem);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition-duration: 0.25s;
    transition-property: opacity, -webkit-transform;
    transition-property: opacity, transform;
    transition-property: opacity, transform, -webkit-transform;
    width: 100%; }
  .button--nav:hover, .button--nav:focus {
    color: #4c4c4c;
    -webkit-transform: translateY(-0.35rem);
            transform: translateY(-0.35rem); }
    .button--nav:hover:before, .button--nav:focus:before {
      -webkit-transform: translateY(0.35rem);
              transform: translateY(0.35rem); }
    .button--nav:hover:after, .button--nav:focus:after {
      opacity: 1;
      -webkit-transform: scaleX(1);
              transform: scaleX(1); }

